//settings
@base: 16;

//sizes
@bp_min: 304;
@bp_med: 704;
@bp_max: 1200;

@lower_lh: 23;
@upper_lh: 33;

@c_1: #7A75FF; //purple
@c_2: #FF6175; //red
@c_3: #00B7D9; //blue
@c_4: #FFA000; //yellow
@c_5: #2EB19F; //sea green

@c_lightgrey: #F5F5F5;

//shared styles
@import '_shared/fonts';
@import '_shared/utils';
@import '_shared/layout';

html, body {
  overflow-x: hidden;
  position: relative;//fix the overflow in iOS.
  width: 100%;//fix the overflow in iOS.
}

img {
  max-width: 100%;
}

body {
  font-family: 'Opensans', sans-serif;
  margin: 0;
}


//should these styles live somewhere else?
.patternExample {
  outline: 1px dashed @c_2;
}

.page-type-home {
  background-color: @c_1;
  height: 100vh;
  background-image:  radial-gradient(ellipse farthest-corner at 0% 100%, darken(@c_1,10%) 20%, darken(@c_2,0%) 100%);
  animation-name: colourpulse;
  animation-duration: 120s;
  animation-iteration-count: infinite;
}


//modules
@import 'modules/m_pagination/m_pagination';
@import 'modules/m_navigation/m_navigation';
@import 'modules/m_figure/m_figure';
@import 'modules/m_prose/m_prose';
@import 'modules/m_pageTitle/m_pageTitle';
@import 'modules/m_icon/m_icon';
@import 'modules/m_siteHeader/m_siteHeader';
@import 'modules/m_introduction/m_introduction';
@import 'modules/m_teaser/m_teaser';
@import 'modules/m_breadcrumbs/m_breadcrumbs';
@import 'modules/m_prevNext/m_prevNext';

//containers
@import 'containers/c_page/c_page';
@import 'containers/c_header/c_header';
@import 'containers/c_fullscreen/c_fullscreen';
@import 'containers/c_grid/c_grid';
